html {
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
img {
  max-width: 100%;
  height: auto;
}

body {
  @include font(16px, 24px);
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-family: $font-main;
  color: $font-main-color;
  font-weight: 400;
}
